import { NextResponse } from "next/server";
import type { NextRequest } from "next/server";

export function middleware(req: NextRequest) {
  // 1. Get the token from the cookies
  const token = req.cookies.get("session_token")?.value;
  const isAuthPage = req.nextUrl.pathname.startsWith("/login");
  const isDashboardPage = req.nextUrl.pathname.startsWith("/dashboard");

  // 2. If trying to access dashboard without a token, redirect to login
  if (isDashboardPage && !token) {
    return NextResponse.redirect(new URL("/login", req.url));
  }

  // 3. If user is already logged in and tries to go to login page, send to dashboard
  if (isAuthPage && token) {
    return NextResponse.push(new URL("/dashboard/profile", req.url));
  }

  return NextResponse.next();
}

// Ensure the matcher covers your protected routes
export const config = {
  matcher: ["/dashboard/:path*", "/login"],
};